<template>
    <div class="x-translator">
        <v-btn v-for="locale of $i18n.availableLocales" :key="locale" @click="onClick(locale)" icon color="primary" class="mx-1">
            <v-img :src="require(`@/assets/imgs/flags/${locale.toLowerCase()}.svg`)" max-width="32"></v-img>
        </v-btn>
    </div>
</template>

<script>
export default {
    name: 'Translator',
    methods: {
        onClick(locale, $event) {
            this.$i18n.setLocale(locale);
            this.$emit('click', locale, $event);
        }
    },
}
</script>
